import {mapMutations} from 'vuex';
export default{
	data(){
		return {
			form: {
				phone: '17688197770',
				password: '123456'
			}
		}
	},
	computed: {
		isOk(){
			return this.form.phone && this.form.password
		}
	},
	onShow(){
		uni.clearStorage();
	},
	methods: {
		async login(){
			// 数据校验
			const {phone,password} = this.form;
			if(!phone)return;
			if(!password)return;
			
			// 执行登录
			uni.showLoading({
				title:"登录中,请稍等"
			})
			
			const userInfo =await this.$API.reqLogin(phone,password);
			
			uni.hideLoading();
			
			uni.setStorage({
				key:"userInfo",
				data:userInfo
			})
			uni.setStorage({
				key:"token",
				data:userInfo.token
			})
			
			uni.reLaunch({
				url:"/pages/index/index"
			})
		},
		...mapMutations(["SETUSERINFOMUTATION"])
	}
}
